<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <div v-for="(o) in content" :key="o">
        <div v-for="(a) in o" :key="a">
          <router-link :to="'/' + type +'?id=' + a.id">
            <div class="introduce">
              {{a.title}}
              <br/>
              {{a.remark}}
            </div>
          </router-link>
        </div>
      </div>
      </el-row>
    </el-card>
  </div>
</template>

<script>
import { article } from '../../../static/js/article.js'
import { tool } from '../../../static/js/tool.js'
import { project } from '../../../static/js/project.js'
import { vedio } from '../../../static/js/vedio.js'
import { doc } from '../../../static/js/doc.js'

export default {
  data () {
    return {
      type: this.$route.query.type,
      content: []
    }
  },
  created () {
    this.initData()
  },
  methods: {
    initData: function () {
      console.log(this.type)
      switch (this.type) {
        case 'article':
          this.content = article
          break
        case 'tool':
          this.content = tool
          break
        case 'project':
          this.content = project
          break
        case 'vedio':
          this.content = vedio
          break
        case 'doc':
          this.content = doc
          break
      }
    }
  }
}
</script>

<style scoped>
.introduce {
  padding: 10px;
  background-color: #EFF2F7;
  margin-bottom: 10px;
  border: solid 1px #F3F3F3;
  line-height: 23px;
  height: 100px;
}
</style>



